  --main_dark2: rgb(60, 60, 65);
  --main_dark3: rgba(40,40,43,0.2);
  --main_light: rgb(255,255,255);
  --shadow: rgb(206,200,205);
  --neon: rgb(255,170,33);
  --neon2: rgba(255,170,33,0.3);
  --details: rgb(94,45,150);
  --details2: rgba(94,45,150,0.3);
  --additional: rgb(78, 189, 237);
}
* {
	box-sizing: border-box;
	-webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-tap-highlight-color: transparent;
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Sora', Verdana, sans-serif;
  font-family: var(--main_font);
  color: rgb(255,255,255);
  color: var(--main_light);
  height: 100vh;
  background-color: rgb(40,40,43);
  background-color: var(--main_dark);
  min-width: 350px;
}
a {
  text-decoration: none;
  color: rgb(255,255,255);
  color: var(--main_light);
  font-family: 'Sora', Verdana, sans-serif;
  font-family: var(--main_font);
}
ul {
  list-style: none;
}
.app {
  padding: 1%;
  width: 100vw;
  height: 100vh;
  margin: 0 auto;
}
.home_icon {
  position: absolute;
  z-index: 100;
  max-width: 60px;
  height: auto;
  opacity: 0.5;
  transition: 0.4s ease;
}
.home_icon:hover {
  opacity: 1;
}
.container {
  width: 90%;
  min-height: 85vh;
  height: auto;
  margin: 10px auto;
}
.center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
footer {
  width: 90%;
  margin: 0 auto;
  text-align: center;
  font-size: 0.9rem;
  padding: 20px 0;
}
label {
  padding-right: 10px;
}
input {
  border: none;
  border-bottom: 1px solid rgba(255,170,33,0.3);
  border-bottom: 1px solid var(--neon2);
  background: inherit;
  outline: none;
  color: rgb(255,255,255);
  color: var(--main_light);
  font-family: 'Sora', Verdana, sans-serif;
  font-family: var(--main_font);
  font-size: 1em;
}
input:focus {
  border: none;
  border-bottom: 1px solid rgb(255,170,33);
  border-bottom: 1px solid var(--neon);
}
button {
  cursor: pointer;
  font-family: 'Sora', Verdana, sans-serif;
  font-family: var(--main_font);
  text-transform: uppercase;
  border:0;
  outline:none;
  box-shadow:none;
  transition: 0.4s;
}
.hide {
  display: none;
}
@media (max-width: 700px) {
  .container {
    width: 100%;
  }
}
@media (max-width: 450px) {
  footer {
    font-size: 0.7rem;
  }
  .home_icon {
    max-width: 40px;
  }
}
